@use 'sass:color';
@import '../../../schedulerpro-sass/themes/vars/classic-dark-vars';

$percentcircle-background-color  : #38383D;

$indicator-dates-color           : $grid-cell-border-color;
$indicator-dates-hover-color     : color.adjust($grid-cell-border-color, $lightness: -20%);

$gantt-inactive-task-label-color : color.adjust($event-labels-color, $lightness: -40%);

$parent-area-background          : #00000025;

$scrollbutton-color              : #66696b !default;
$scrollbutton-hover-color        : color.adjust($scrollbutton-color, $lightness: 25%) !default;

$gantt-unscheduled-tasks-color   : color.adjust(#66696b, $lightness : 25%) !default;
